
#login {
  padding: 70px 0px;
}
#login > div.bg {
    background: url("http://i062.radikal.ru/1310/58/ac4eaa31efdf.jpg") #000000;
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    -webkit-filter: blur(5px);
}
#login > div.bg:after {
      content: '';
      background: rgba(0, 49, 45, 0.75);
      width: 100%;
      height: 100%;
      position: absolute;
}
#login > .row {
    max-width: 800px;
    margin: 0 auto;
    background: url(http://i062.radikal.ru/1310/58/ac4eaa31efdf.jpg);
    background-size: cover;
    display: -webkit-box;
    position: relative;
    -webkit-box-shadow: 0 10px 30px 0px rgba(0, 0, 0, 0.6);
            box-shadow: 0 10px 30px 0px rgba(0, 0, 0, 0.6);
}
#login > .row > div {
      background: rgba(255, 255, 255, 0.95);
}
#login > .row > div + div {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      color: #FFF;
      background: rgba(0, 49, 45, 0.75);
}
#login > .row img {
      max-width: 100px;
}
#login .o-ers {
    border-bottom: 1px solid #e7edee;
    padding: 0.5rem;
}



